import React from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';

import 'materialize-css/dist/css/materialize.min.css';

import Navegacion from './components/Navegacion';
import Index from './components/Index';
import Medicamentos from './components/Medicamentos'
import Login from './components/Login';


import './App.css';
import Noticias from './components/Noticias';
import Pmascarillas from './components/Pmascarillas';
import Lsintomas from './components/Isintomas';



function App() {
  return (
    <Router> 

      <Navegacion/>

      <Route path="/" exact component={Index} />
      <Route path="/Login" exact component={Login} />
      <Route path="/Medicamentos" exact component={Medicamentos} />
      <Route path="/Lsintomas" exact component={Lsintomas} />
      <Route path="/Pmascarillas" exact component={Pmascarillas} />
      
      <Route path="/Noticias" exact component={Noticias} />
      
    </Router>

  );
}

export default App;
