import React, { Component } from 'react'
import Map from './Map';


//se inicializa el estado para que rederize que es lo primero que hace react
//no muestre un error de que el nombre esta indefinido ya que hasta despues hace el ciclo de vida de la aplicacion
export default class Buscador extends Component {
    constructor(props){
        super(props);
        this.state = { 
        meds: [0],
        medsBackup:[0],
        textoBuscar:'' 
        
    }}
    componentDidMount() {
     
        
        fetch('https://prueba-protitpo.firebaseio.com/.json')
            .then(data => data.json())
            .then(data => {                
                this.setState({                    
                    meds: data,
                    medsBackup:data                
                })
            })          
            
        }
        
    
   filter = (event)=>{
        //obtener el texto del input del buscador
        var text = event.target.value;
        //muestra el texot del input del buscador
        console.log(text);
        //obtener datos del array del stado
        const data = this.state.medsBackup;

        
        console.log(this.state.medsBackup);
        console.log(data);
     
      switch (text) {
          case 'insulina':
            var  newData = data.filter(data => data.medicamentos.insulina === "si" );
             this.setState({
                 meds: newData
             });
              break;
            case 'acetaminofen':
                var  newData = data.filter(data => data.medicamentos.acetaminofen === "si" );
                this.setState({
                    meds: newData
                });
            break;
            case 'zinc':
            var  newData = data.filter(data => data.medicamentos.zinc === "si" );
             this.setState({
                 meds: newData
             });  
            break;
            case 'vitamina d':
                var  newData = data.filter(data => data.medicamentos.vitaminad === "si" );
             this.setState({
                 meds: newData
             });
            break; 
          default:
              break;
      }

      this.setState({
        
        textoBuscar: text
        })

        //aplica un filtro para buscar el dato con el id 1
        //let newData = data.filter(data => data.medicamentos.insulina === "si" );

        console.log(newData);
        

        /*this.setState({
            meds: newData,
            textoBuscar: text
        })*/

        console.log( this.state.meds);
       // console.log(this.state.meds[0].nombre);
        console.log(this.state.textoBuscar);
        

        

      }

    render() {
        const datos = this.state.medsBackup;
        return (
            <div>
                <div className="container">
                    <div className="card-panel">
                        <div className="row">
                            <form className="col s12"/>
                                    <div class="input-field col s6">
                                    {/*<input id="Farmaco" type="text" className="validate"/>*/}
                                    <label for="Farmaco">Ingrese el medicamento que desea encontrar  </label>                               
                                    <br></br>
                                    <input class="form-control"  value={this.state.textoBuscar} onChange={(text) => this.filter(text)}/>
                                    {/*<input class="form-control"  value={this.state.textoBuscar } />*/}
                                   {/* <button onClick={(text) => this.filter(text)}>
                                        Buscar
                                    </button>*/}

                                    <div>
                                    {this.state.meds
                                    ? (<div>meds tiene datos -- {this.state.meds[0].nombre}</div>)
                                    : (<div>negative</div>)}
                                    </div>
                                   
                                    {datos[0].nombre}


                                    <Map meds={this.state.meds}/>    
                                  
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

